<template>
  <div>
    <div v-if="pageloading" class="text-center">
      <i class="fa fa-spinner fa-spin fa-2x" />
    </div>
    <b-card v-else>
      <b-row>
        <b-col>
          <label class="label">
            {{ $t("TASKINFORMATION") }}
          </label>
        </b-col>
      </b-row>
      <b-form>
        <div style="overflow: auto">
          <b-row class="ml-2 d-flex align-items-center justify-content-between">
            <b-col class="pl-0 pr-0 mb-1">
              <b-input-group
                :prepend="$t('TASK.TASKNAME')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form11"
                  v-model="taskDetailsInfo.taskName"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </b-col>
            <!-- cols="12" sm="6" lg="3" -->
            <b-col class="pl-0 pr-0 mb-1" cols="12" sm="6" lg="3">
              <b-input-group
                :prepend="$t('TASK.PROCESSINGDATETIME')"
                class="mb-2 mr-sm-2 mb-sm-0 tableManner"
              >
                <b-input
                  id="inline-form12"
                  v-model="taskDetailsInfo.processingDateTime"
                  class="backgroundDisable pr-0 pl-1"
                  disabled
                ></b-input>
              </b-input-group>
            </b-col>
            <b-col class="pl-0 pr-0 mb-1">
              <div class="d-flex align-items-center justify-content-between">
                <span class="p-2 w-70 input-group-text modalSelection"
                  >{{ $t("TASK.PERSONINCHARGE") }}
                </span>
                <span class="w-30 mr-1">
                  <b-input-group class="d-flex justify-content-between w-220">
                    <b-input
                      v-model="taskDetailsInfo.personInCharge"
                      class="backgroundDisable pr-0 pl-2 modalWork"
                      disabled
                    ></b-input>
                    <b-button
                      :disabled="!getContentAccess()"
                      @click="showPersonModal"
                      size="sm"
                      variant="primary"
                      class="btn-primary d-inline-block modalButton"
                      >{{ $t("TASK.PERSONINCHARGESELECT") }}</b-button
                    >
                  </b-input-group>
                </span>
              </div>
            </b-col>
            <b-col class="pl-0 pr-0 mb-1">
              <b-input-group
                :prepend="$t('TASK.TASKSTATUS')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-form-select
                  v-model="taskDetailsInfo.taskStatus"
                  :disabled="!getContentAccess()"
                  :options="options"
                ></b-form-select>
              </b-input-group>
            </b-col>
          </b-row>
        </div>
      </b-form>
      <br />
      <b-button
        :disabled="!getContentAccess()"
        @click="updateTaskData"
        variant="primary"
        >{{ $t("UPDATE") }}<i v-if="loading" class="fa fa-spinner fa-spin"
      /></b-button>
      <hr class="hrBarType" />
      <b-row>
        <b-col>
          <label class="label">
            {{ $t("DETAILSINFORMATION") }}
          </label>
        </b-col>
      </b-row>
      <b-form>
        <b-row>
          <b-col cols="12" xl="6">
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.REQUESTDATETIME')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form1"
                  v-model="expenseDetailsInfo.requestDateTime"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.EXPENSEITEM')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form2"
                  v-model="expenseDetailsInfo.expenseItemName"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.COMPANYNAME')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form3"
                  v-model="expenseDetailsInfo.companyName"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.INDUSTRY')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form4"
                  v-model="expenseDetailsInfo.industry"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.PREFECTURES')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form5"
                  v-model="expenseDetailsInfo.prefectures"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
          </b-col>
          <b-col cols="12" xl="6">
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.EXPENSEITEMAMOUNT')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form6"
                  v-model="expenseDetailsInfo.expenseItemAmount"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
            <div class="bindInputGroup">
              <b-input-group
                :prepend="$t('TASK.REDUCEABLEAMOUNT')"
                class="mb-2 mr-sm-2 mb-sm-0"
              >
                <b-input
                  id="inline-form7"
                  v-model="expenseDetailsInfo.reduceableAmount"
                  class="backgroundDisable"
                  disabled
                ></b-input>
              </b-input-group>
            </div>
            <div class="bindInputGroup">
              <b-button
                @click="goToReductionProposal"
                variant="primary"
                class="float-right"
                size="md"
                >{{ $t("CUSTOMERSERVICE") }}</b-button
              >
            </div>
          </b-col>
        </b-row>
      </b-form>
    </b-card>
    <b-modal
      id="person-modal"
      ref="person-modal"
      :hide-footer="true"
      centered
      variant="secondary"
      modal-class="py-0"
      body-class="py-0"
      size="md"
      footer-class="border-0"
      header-class="border-0 bg-white pd-0"
      button-size="sm"
      hide-header-close
    >
      <template v-slot:modal-header="{ cancel }">
        <div class="title-fw title-fs text-dark ml-2 mt-2">
          {{ $t("SELECTCONTACT") }}
        </div>
        <div class="w-75 d-flex justify-content-end">
          <b-button
            @click="cancel"
            class="back-btn close-btn mt-2"
            variant="primary"
            >{{ $t("CLOSE") }}</b-button
          >
        </div>
      </template>
      <b-card class="pop-up-box">
        <b-row>
          <b-col>
            <b-form-group>
              <b-form-radio-group
                id="radio-group-1"
                v-model="selectedPersonID"
                :options="searchSelect"
                class="radioControl mb-3 pt-2"
                size="md"
                stacked
              ></b-form-radio-group>
            </b-form-group>
          </b-col>
        </b-row>
      </b-card>
      <b-row>
        <b-col cols="12" class="text-center mb-2">
          <b-button
            @click="getSelectedPerson"
            variant="primary"
            class="float-right"
            >{{ $t("SELECT") }}
          </b-button>
        </b-col>
      </b-row>
    </b-modal>
  </div>
</template>
<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDateHourMinute } from "@/shared/utils";
import { mapGetters } from "vuex";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "TaskDetails",
  components: {},
  data() {
    return {
      selectedPerson: {},
      selectedPersonID: "",
      searchSelect: [],
      defaultDataItem: {
        item: "",
        value: "",
      },
      pageloading: false,
      loading: false,
      getAccessControl: true,
      options: [
        { value: "01", text: this.$t("UNPROCESSED") },
        { value: "02", text: this.$t("PROCESSING") },
        { value: "03", text: this.$t("PROCESSED") },
      ],
      taskType: {
        "001": this.$t("REQUESTQUOTATIONTASK"),
        "002": this.$t("CONTRACTTASK"),
      },
      taskDetailsInfo: {},
      expenseDetailsInfo: {},
    };
  },
  computed: {
    ...mapGetters({
      task_data: "getTaskDetails",
    }),
  },
  validations: {},
  mounted() {},
  created() {
    this.getPersonIncharge();
    this.getTaskDetails();
  },
  methods: {
    showPersonModal() {
      this.selectedPersonID = "";
      this.$refs["person-modal"].show();
    },
    async getPersonIncharge() {
      let personID = localStorage.getItem("personCompanyID");
      const { data } = await BasicInfoApis.postAPI(endPoint.MASTERREADMPERSON, {
        fieldlist: ["person_id", "person_name"],
        conditions: [
          {
            key: "person_company",
            value: personID,
          },
        ],
      });
      let responseData = data.data;
      this.searchSelect = [];
      for (let i = 0; i < responseData.length; i++) {
        this.searchSelect.push({
          value: responseData[i].person_id,
          text: responseData[i].person_name,
        });
      }
    },
    getSelectedPerson() {
      this.$refs["person-modal"].hide();
      let item = this._.find(this.searchSelect, {
        value: this.selectedPersonID,
      });
      if (item) {
        this.selectedPerson = {
          personID: item.value ? item.value : "",
          personName: item.text ? item.text : "",
        };
        this.taskDetailsInfo.personInCharge = this.selectedPerson.personName;
      }
    },
    getContentAccess() {
      let person_auth = localStorage.getItem("person_auth");
      let personID = this.taskDetailsInfo.taskPersonId;
      let localPerson = localStorage.getItem("personID");
      return (
        person_auth == "02" || (personID != null && personID == localPerson)
      );
    },
    getDateFormat(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) +
        "/" +
        date.slice(5, 7) +
        "/" +
        date.slice(8, 10) +
        " " +
        date.slice(11, 16)
      );
    },
    goToReductionProposal() {
      localStorage.setItem("activeTab", 10);
      this.$store.dispatch("setClientData", {
        client_name: this.expenseDetailsInfo.companyName,
        client_no: this.expenseDetailsInfo.client_no,
      });
      this.$router.push({ name: "client_process" });
    },
    async updateTaskData() {
      this.loading = true;
      let updateTask = {
        conditions: {
          task_no: this.taskDetailsInfo.taskNo,
          task_type: this.taskDetailsInfo.taskType,
        },
        data: {
          task_kbn: this.taskDetailsInfo.taskStatus,
          task_person_id: this.selectedPerson.personID
            ? this.selectedPerson.personID
            : this.taskDetailsInfo.taskPersonId,
          task_body_no: this.taskDetailsInfo.taskBodyNo,
          task_dt: getDateHourMinute(new Date()),
          update_id: localStorage.getItem("personID"),
          update_dt: getDateHourMinute(new Date()),
        },
      };
      const { data } = await BasicInfoApis.postAPI(
        endPoint.TASKUPDATETASKANDTYPETABLE,
        updateTask
      );
      this.loading = false;
      if (data.success) {
        this.$router.push({ name: "admin_dasboard" });
      }
    },
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    bindTaskPersonData() {
      let personID = this.taskDetailsInfo.taskPersonId;
      let dataFound = this._.find(this.searchSelect, { value: personID });
      if (dataFound) {
        this.defaultDataItem = dataFound;
      } else {
        let mapData = {
          text: this.taskDetailsInfo.personInCharge
            ? this.taskDetailsInfo.personInCharge
            : "",
          value: this.taskDetailsInfo.taskPersonId
            ? this.taskDetailsInfo.taskPersonId
            : "",
        };
        this.searchSelect.push(mapData);
        this.defaultDataItem = mapData;
      }
    },
    async getTaskDetails() {
      this.pageloading = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.TASKTASKDETAILCOSTREDUCTION,
        {
          task_no: this.task_data.task_no,
          task_type: this.task_data.task_type,
          client_no: this.task_data.client_no,
        }
      );
      this.pageloading = false;
      let responseData = data.data[0];
      this.taskDetailsInfo = {
        taskNo: responseData.task_no,
        taskType: responseData.task_type,
        taskBodyNo: responseData.task_body_no,
        taskName: responseData.task_type
          ? this.taskType[responseData.task_type]
          : "",
        taskPersonId: responseData.task_person_id
          ? responseData.task_person_id
          : "",
        processingDateTime: responseData.task_dt
          ? this.getDateFormat(responseData.task_dt)
          : "",
        personInCharge: responseData.person_name
          ? responseData.person_name
          : "",
        taskStatus: responseData.task_kbn,
      };
      this.expenseDetailsInfo = {
        client_no: responseData.client_no,
        expenseItemName: responseData.expense_item_nm
          ? responseData.expense_item_nm
          : "",
        companyName: responseData.client_name ? responseData.client_name : "",
        industry: responseData.industry_nm ? responseData.industry_nm : "",
        prefectures: responseData.prefectures ? responseData.prefectures : "",
        requestDateTime: responseData.create_dt
          ? this.getDateFormat(responseData.create_dt)
          : "",
        expenseItemAmount: responseData.cost
          ? this.filterByCurrency(parseFloat(responseData.cost)) +
            this.$t("YEN")
          : "",
        reduceableAmount: responseData.cost
          ? this.filterByCurrency(
              parseFloat(responseData.cost) -
                parseFloat(responseData.reduction_proposal_cost)
            ) + this.$t("YEN")
          : "",
      };
    },
  },
};
</script>
<style scoped lang="scss">
#Label {
  font-size: 140%;
  font-weight: 700 !important;
}
.form-control {
  border-radius: inherit;
  height: 40px;
}
.tableManner {
  margin-left: -2px;
}
.modalSelection {
  margin-left: -4px;
  margin-right: -1px;
}

.modalWork {
  width: 220px;
  margin-right: -10px;
}
.modalButton {
  margin-left: -100px;
  float: left !important;
  margin-top: 5px;
  z-index: 1;
  height: 30px !important;
}
.card {
  border: 1px solid #918c8c;
}
.custom-select {
  height: 40px;
  border-color: rgb(163, 163, 163);
}
.bindInputGroup {
  padding-bottom: 10px;
}
.input-group-text {
  height: 40px !important;
  width: 98px;
  font-weight: 700;
  background-color: #ffff;
  border-color: rgb(163, 163, 163);
  border: 2px solid rgb(163, 163, 163);
  padding: 0.175rem 0.45rem !important;
}
.backgroundDisable {
  background: #ffffff !important;
}

@media (min-width: 576px) {
  .mr-sm-2,
  .mx-sm-2 {
    margin-right: -0.2rem !important;
  }
}
@media only screen and (max-width: 773px) {
  .modalSelection {
    margin-left: 0px !important;
    margin-right: -1px;
  }
}
@media only screen and (max-width: 553px) {
  .tableManner {
    margin-left: 0px !important;
  }
}

.hrBarType {
  height: 2px;
  border-width: 7;
  margin-left: -20px;
  margin-right: -20px;
  color: #918c8c;
  background-color: gray;
}
.updateRow {
  display: flex;
}
.w-220 {
  width: 220px !important;
}
.searchSelect1 {
  position: relative !important;
  flex: 1 1 auto !important;
  width: 1% !important;
  margin-bottom: 0 !important;
  border: 1px solid #a9a6a7 !important;
  white-space: nowrap !important;
}
</style>